@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap');

/* CSS RESET */

*, *::before, *::after {
    box-sizing: border-box;
  }

  * {
    margin: 0;
  }
  
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }

  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }

  input, button, textarea, select {
    font: inherit;
  }

  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }

  /* APP */
  body {
    background-color: #07000d;
    background-image: url("http://www.transparenttextures.com/patterns/cartographer.png");
    color: white;
    font-family: 'Rubik', sans-serif;
  }

  /* container */
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }

  /* todo list */
  .title {
    text-align: center;
    padding: 3rem, 0, 1rem;
    font-size: 3rem;
  }

  .todos {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .todo {
    display: flex;
    justify-content: space-between;
    padding: 1rem 1.25rem;

    background-color: rgba(255, 255, 255, 0.1);
    width: 50%;
    font-size: 1.4rem;
    border-radius: 1rem;
    transition: all 0.2s;

    &:hover {
      background-color: rgba(255, 255, 255, 0.2);
    }
  }

  .mutations {
    display: flex;
    gap: 0.75rem;
  }

  .todo_status,
  .todo_delete {
    border: note;
    padding: 0;
    line-height: 1;
    cursor: pointer;
    transition: all 0.2s;
    background: none;
    color: white;
    font-size: 1.6rem;

    &:hover {transform: scale(1.15);}
  }

  .todo_status {
    font-size: 1.75rem;
  }

  .todo_delete {
    font-size: 1.75rem;
  }

  /* form */
  .form {
    display: flex;
    gap: 0.5rem;
    width: 50%;
    margin: 1rem auto;
  }

  .form_input {
    flex: 1;
    padding: 0.25rem 0.5rem;
    border-radius: 0.25rem;
    border: none;
    outline: none;

    &:focus,
    &:hover {
      background-color: rgba(255, 255, 255, 0.85);
    }
  }

  .form_button {
    border: none;
    background-color: rgba(113, 0, 212, 0.7);
    cursor: pointer;
    color: white;
    border-radius: 0.25rem;
    transition: all 0.2s;

    &:hover {
      background-color:  rgba(95, 0, 179, 0.5);
    }
  }

  @media (max-width: 450px) {
    .title {
      font-size: 2.5rem;
    }
    .form {
      width: 95%;
    }
    .todo {
      width: 95%;
      font-size: 1.2rem;
    }
  }